export default {
	//    
	getFullTime(a) {
	  let b = new Date(a)
	  var y = b.getFullYear()
	  var mon = b.getMonth() + 1
	  var day = b.getDate()
	  var h = b.getHours()
	  h = h < 10 ? '0' + h : h
	  var m = b.getMinutes()
	  m = m < 10 ? '0' + m : m
	  var s = b.getSeconds()
	  s = s < 10 ? '0' + s : s
	  // return y + '' + mon + '' + day + '' + h + ':' + m + ':' + s
	  return y + '' + mon + '' + day + ' ' + h + ':' + m
	},
	// 
	jump(url, type = 1) {
		// 
		if (type == 1) {
			uni.navigateTo({
				url: url
			})
		}

		// 
		if (type == 2) {
			uni.redirectTo({
				url: url
			})
		}
		// 
		if (type == 3) {
			uni.reLaunch({
				url: url
			})
		}

		//  tabBar  tabBar 
		if (type == 4) {
			uni.switchTab({
				url: url
			})
		}
	},
	tostring(value){
		return JSON.stringify(value);
	},
	 /**
	   * --111
	   * @param {*} phoneNumber //
	   */
	isPhoneNum(phoneNumber) {
	    if (typeof phoneNumber !== 'string') {
	      console.error('string');
	      return false;
	    }
	    ;
	    return /^1[\d]{10}$/.test(phoneNumber);
	},
	
	isNumber(value) {         //
	    var patrn = /^(-)?\d+(\.\d+)?$/;
	    if (patrn.exec(value) == null || value == "") {
	        return false
	    } else {
	        return true
		}
	}
}
